.bottomlist{
    @include d-flex(space-between,center);
    flex-direction: row;
    width: 100%;
    height: 50px;
    padding: 0 1rem 0 1rem;
    &>p{
        font-size: 0.8rem;
    }
    &--center{
        @include d-flex(space-around,center);
        flex-direction: row;
        width: 40%;
        &>p{
            font-size: 0.8rem;
        }
    }

}